* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

img {
  display: block;
}

.country-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.country-item,
.country-name {
  display: flex;
  flex-direction: row;
  gap: 10px;
  align-items: center;
}

.country-item {
  font-size: 20px;
  font-weight: 500;
}

.country-name {
  font-size: 35px;
  font-weight: 600;
}

.country-title {
  font-weight: 500;
}
